/* style sheet for navigation bar*/
.navbar{
    display: flex;
    justify-content: space-around;
    padding: 10px;
    box-shadow: 0 1px 3px -2px black;
}

.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
    
}
.nav-logo p{
    color: brown;
    font-size: 25px;
    font-weight: 600px;
}

.nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: black;
    font-size: 20px;
    font-weight:500;
    margin-left:20px;
}

.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}

.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #FF4141;
    margin:0;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}
.file-upload {
    display: flex;
    align-items: center;
}

.upload-btn {
    width: 250px;
    height: 50px;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    background: #ffffff;
    color: #515151;
    font-size: 20px;
    cursor: pointer;
    transition: background 0.3s ease;
    border-color:#7a7a7a;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-top: 20px;
    
}


.nav-login-cart button{
    width: 150px;
    height: 50px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #151515;
    font-size: 20px;
    background: #ffffff;
    cursor: pointer;
    border-color:#7a7a7a;
}
.nav-login-cart button:active{
    background: #f3f3f3;
}
.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;
}