.offers{
    width: 65%;
    height:60vh;
    display:flex;
    margin:auto;
    padding: 0px 140px;
    margin-bottom: 150px;
}
.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.offers-left h1{
    color:black;
    font-size: 80px;
    font-weight: 600;

}

.offers-left p{
    color:black;
    font-size: 22px;
    font-weight: 600;
    
}

.offers-left button{
    width:282px;
    height:70px;
    border-radius:35px;
    background: blue;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top:30px;
    cursor:pointer;
}

.offers-right{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}